.Banner {
  margin-bottom: calc(15px + 2vmin);
  padding: 10px;
  font-size: calc(15px + 2vmin);
}

.container-roomConnect {
  width: 75%;
  max-width: 300px;
}

.container-room {
  width: 75%;
  max-width: 300px;
}

.container-roomConnect > * {
  margin-bottom: calc(20px + 2vmin);
}

.container-colourPicker {
  margin: calc(20px + 2vmin);
}

.palette-colourPicker {
  margin-top: calc(10px + 2vmin);
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(4, 1fr);
  height: 150px;
  width: 200px;
  margin-left: auto;
  margin-right: auto;
}

.item-colourPicker, .item-playerList {
  border-radius: 100%;
}

.item-colourPicker.unavailable {
  filter: saturate(0.5);
  opacity: 0.2;
}

.item-playerList {
  width: calc(20px + 2vmin);
  height: calc(20px + 2vmin);
  position: absolute;
  right: 0;
  top: 50%;

  -webkit-transform: translate(0, -50%);
  -moz-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  -o-transform: translate(0, -50%);
  transform: translate(0, -50%);
}

.name-playerList {
  position: absolute;
  left: 0;
  top: 50%;

  -webkit-transform: translate(0, -50%);
  -moz-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  -o-transform: translate(0, -50%);
  transform: translate(0, -50%);
}

.row-playerList {
  height: calc(30px + 2vmin);
  width: 200px;
  position: relative;
  margin: auto;
}

.modal-body {
  position: absolute;
  top: 50%;
  left: 50%;

  background-color: #fff;

  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);

  height: 200px;
  width: 300px;
  padding: 20px;

  display: inline-block;
}

.modal-invalid-content {
  position: absolute;
}

.modal-invalid-button {
  position: absolute;
  top: 90%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
